import React from 'react';
import { Row, Col } from 'antd';

import "./DataShowView.less";

import { Table } from 'antd';
import { filter } from 'lodash';



function onChange(pagination, filters, sorter, extra) {
  // console.log('params', pagination, filters, sorter, extra);
}




export default class DataShowView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }

  componentDidMount() {
  }


  render() {
    const { intl, data, schema, collapsedRight, editstate, editingCurrentFact } = this.props;
 
    let datares = [];
    let currentSubspace=[];

    if(editstate && editingCurrentFact.subspace.length>0){
      let dataclone1 =_.cloneDeep(this.props.dataresLocal)
      let datares1=[]
        currentSubspace=editingCurrentFact.subspace
        currentSubspace.map((item,index)=>{
          if(index===0){
          datares1.push(...dataclone1.filter(filter=>filter[item.field]===item.value))
          dataclone1=datares1
          }else{
            datares1=dataclone1.filter(filter=>filter[item.field]===item.value)
            dataclone1=datares1
          }

        })
        datares=datares1
      }
    else if(editstate && editingCurrentFact.subspace.length===0){
         datares = _.cloneDeep(this.props.dataresLocal)
          
    }else{
      // filters.map((item, index) => {
      //   datares = [];
      //   let itemfield = item.field;//Brand
      //   item.value.map(item1 => {//BMW,Ford
      //     if (schemaNoTextID[index].type === "numerical") datares.push(...dataclone.filter(filter => filter[itemfield] == parseFloat(item1)))
      //     else datares.push(...dataclone.filter(filter => filter[itemfield] == item1))

      //   })
      //   dataclone = datares;

      // })
      // // this.props.updateDatares(datares);
      datares=this.props.datares;
    }


    const columns = []
   
    if (data.length > 0) {
      for (let i = 0; i < data.columns['length']; i++) {
        columns.push({
          title: `${data.columns[i]}`,
          dataIndex: `${data.columns[i]}`,
          render: (text) => {
            return (
              <div className="scrolls">{text}</div>
            )
          }
          // ellipsis: true //tableLayout="fixed"
        });
      }

      for (let i = 0; i < data.length; i++) {
        data[i]['key'] = i
      }

    }
    if (!collapsedRight) {
      return (
        <div className="tableContent">
          <Table columns={columns} dataSource={datares} onChange={onChange} pagination={{ showSizeChanger: false, pageSize: 30, size: 'small' }} scroll={{ x: "300px" }} />
        </div>
      )
    } else return null
  }
}
